/* eslint-disable no-unused-vars */

/**
 * Enum representing different areas in Speccer.
 */
export enum SpeccerAreaEnum {
  Empty = '',       // Represents an empty area
  Left = 'left',    // Represents the left area
  Right = 'right',  // Represents the right area
  Bottom = 'bottom',// Represents the bottom area
  Top = 'top',      // Represents the top area
}

/**
 * Enum representing different areas in Dissect.
 */
export enum DissectAreaEnum {
  Outline = 'outline', // Represents an outline area
  Enclose = 'enclose', // Represents an enclose area
  Full = 'full',       // Represents a full area
  Left = 'left',       // Represents the left area
  Right = 'right',     // Represents the right area
  Bottom = 'bottom',   // Represents the bottom area
  Top = 'top',         // Represents the top area
  SVG = 'svg',         // Represents an SVG area
  Curly = 'curly',     // Represents a curly area
}

/**
 * Enum representing different measurement areas.
 */
export enum MeasureAreaEnum {
  Width = 'width',     // Represents the width measurement area
  Height = 'height',   // Represents the height measurement area
  Left = 'left',       // Represents the left measurement area
  Right = 'right',     // Represents the right measurement area
  Bottom = 'bottom',   // Represents the bottom measurement area
  Top = 'top',         // Represents the top measurement area
}
